import React from 'react';
 
class Jumbotron extends React.Component {
 
  render() {
 
    return (
 
        <div className="jumbotron">
            <div className="container">
                <h1 className="display-3">Nosotros </h1>
                <p>
                    Una pizzeria familiar con la receta más deliciosa, hechas con amor.
                    <br/>
                    Pizzas, milanesas y las mejores promo del Barrio San Pablo.
                </p>
                <p><a className="btn btn-primary btn-lg" href="https://www.facebook.com/pizzashouse" target="_blank" role="button">Visitar Web</a></p>
            </div>
        </div>
 
    )
    
  }
 
}
 
export default Jumbotron;