import React from 'react';
//import './Slider.css';
 
class Slider extends React.Component {
 
  render() {
 
   return (
 
   <div id="slider" className="carousel slide" data-ride="carousel">
     <ol className="carousel-indicators">
         <li data-target="#slider" data-slide-to="0" className="active"></li>
         <li data-target="#slider" data-slide-to="1"></li>
         <li data-target="#slider" data-slide-to="2"></li>
     </ol>
     <div className="carousel-inner">
         <div className="carousel-item active">
             <img className="img-fluid" src="https://live.staticflickr.com/65535/50114190137_674fa1f797_o.jpg" />
             <div className="elementos">
                 <a className="btn btn-lg btn-primary" href="https://www.pedidosya.com.py/restaurantes/asuncion/pizza-house-yvapovo-1909-menu" target="_blank" role="button">Pedí Ya</a>
             </div>
         </div>
         <div className="carousel-item">
             <img className="img-fluid" src="https://live.staticflickr.com/65535/50114190167_7024894021_o.jpg" />
             <div className="elementos">
                 <a className="btn btn-lg btn-primary" href="https://www.pedidosya.com.py/restaurantes/asuncion/pizza-house-yvapovo-1909-menu" target="_blank" role="button">Pedí Ya</a>
             </div>
         </div>
         <div className="carousel-item">
             <img className="img-fluid" src="https://live.staticflickr.com/65535/50113389873_1e31f17c51_o.jpg" />
             <div className="elementos">
                 <a className="btn btn-lg btn-primary" href="https://www.pedidosya.com.py/restaurantes/asuncion/pizza-house-yvapovo-1909-menu" target="_blank" role="button">Leer más</a>
             </div>
         </div>
     </div>
     <a className="carousel-control-prev" href="#slider" role="button" data-slide="prev">
         <span className="carousel-control-prev-icon" aria-hidden="true"></span>
         <span className="sr-only">Anterior</span>
     </a>
     <a className="carousel-control-next" href="#slider" role="button" data-slide="next">
         <span className="carousel-control-next-icon" aria-hidden="true"></span>
         <span className="sr-only">Siguiente</span>
     </a>
 </div>
 
   )
    
  }
 
}
 
export default Slider;